// --sb-loader-color-primary - Bar color
// --sb-loader-color-background - Container color
// --sb-loader-border-color - Show border

@mixin sb-determinate-value {
  .sb-determinate-bar {
    background-color: var(--sb-loader-color-primary, var(--sbt-theme-color-70));
    height: 8px;
    left: 0;
    position: absolute;
    top: 0;
  }
  @for $i from 1 through 100 {
    .sb-determinate-bar_#{$i} {
      width: $i * 1%;
    }
  }
}

.sb-linear-container {
  @include sb-determinate-value;
  background-color: var(--sb-loader-color-background, transparent);
  border: 1px solid var(--sb-loader-border-color, transparent);
  margin: 20px auto;
  max-height: 24px;
  overflow: hidden;
  position: relative;
  width: 50%;
}

.sb-linear-small {
  width: 25%;
}

.sb-linear-large {
  width: 100%;
}

.sb-indeterminate,
.sb-determinate {
  height: 8px;
  position: relative;
  width: 100%;
}

.sb-determinate {
  background-color: var(--sb-loader-color-primary, var(--sbt-theme-color-20));
}

.sb-determinate-label {
  color: var(--sb-loader-copy-text-color, var(--sbt-theme-color-70));
  font-size: var(--sb-loader-text-size, var(--sbt-font-size-20));
  font-weight: var(--sb-loader-text-weight, var(--sbt-font-weight-70));
  line-height: var(--sb-loader-text-line-height, var(--sbt-line-height-50));
  margin: var(--sbt-spacing-0);
  padding: var(--sbt-spacing-0);
  text-align: center;
}

.sb-indeterminate::before {
  animation: indeterminate_first 1.5s infinite ease-out;
  background-color: var(--sb-loader-color-primary, var(--sbt-theme-color-70));
  content: '';
  height: 100%;
  position: absolute;
}

@keyframes indeterminate_first {
  0% {
    left: -100%;
    width: 30%;
  }

  100% {
    left: 100%;
    width: 30%;
  }
}
